import store from '@/store'
import { timeouts, tutorial } from '../globals/globals'

export default class TutorialService {
  tutorialSetup (i) {
    store.dispatch('game/changeSettings', tutorial.settings)
    store.dispatch('game/changeBoard', tutorial.board)
    store.dispatch('game/changePlayers', tutorial.players)
    store.dispatch('game/changePlayer', tutorial.players[0])
    store.dispatch('game/changeBusy', false)
    store.dispatch('game/changeStatus', 1)
    store.dispatch('settings/changeTutorialStep', 0)
    const step = tutorial.steps[0]
    setTimeout(() => {
      this.tutorialStep(1)
    }, step.delay)
  }

  tutorialStep (i) {
    const stepVal = store.getters['settings/getTutorialStep']
    const step = tutorial.steps[stepVal]
    const selected = store.getters['game/getSelected']
    const board = store.getters['game/getBoard']
    const players = store.getters['game/getPlayers']
    // check if a new move has to be possible
    if (step.newMove) {
      board[step.newMove.from].adjacents = [step.newMove.to]
      store.dispatch('game/changeBoard', board)
    }
    console.log(JSON.stringify(step.values), JSON.stringify([i, selected]))
    // check correct step action
    if (JSON.stringify(step.values) === JSON.stringify([i, selected])) {
      // if action is attack
      if (step.action === 'attack') {
        const defenderId = +JSON.stringify(board[i].owner)
        const results = { attacker: 0, defender: board[i].owner, attackerDice: step.attackerDice, defenderDice: step.defenderDice }
        const attacker = board[selected]
        const defender = board[i]
        store.dispatch('game/changeResults', results)
        store.dispatch('game/changeDefender', i)
        store.dispatch('game/changeBusy', true)
        setTimeout(() => {
          store.dispatch('game/changeDefender', null)
          // if attack is won
          if (results.attackerDice.total > results.defenderDice.total) {
            // add dices to new cell
            defender.dice = attacker.dice - 1
            // change owner of new cell
            defender.owner = 0
            store.dispatch('game/changeCell', { i, data: defender })
            // if more moves can be made from new cell
            if (defender.dice > 1) {
              store.dispatch('game/changeSelected', i)
            } else {
              store.dispatch('game/changeSelected', null)
            }
          } else {
            store.dispatch('game/changeSelected', null)
          }
          // leave one dice behind
          attacker.dice = 1
          store.dispatch('game/changeCell', { i: selected, data: attacker })
          if (step.changeAttacker) {
            players[0].size = step.changeAttacker
          }
          if (step.changeDefender) {
            players[defenderId].size = step.changeDefender
          }
          store.dispatch('game/changePlayers', players)
          this.nextStep(i)
        }, timeouts.executeAttack * 2)
      } else {
        this.nextStep(i)
      }
    }
  }

  nextStep (i) {
    const stepVal = store.getters['settings/getTutorialStep']
    setTimeout(() => {
      store.dispatch('settings/changeTutorialStep', (stepVal + 1))
      const step = tutorial.steps[stepVal + 1]
      if (step.delay) {
        setTimeout(() => {
          this.tutorialStep(i)
        }, step.delay)
      } else {
        store.dispatch('game/changeBusy', false)
      }
    }, timeouts.tutorial)
  }

  endTutorial () {
    const stepVal = store.getters['settings/getTutorialStep']
    store.dispatch('settings/changeTutorialStep', (stepVal + 1))
    setTimeout(() => {
      let board = store.getters['game/getBoard']
      board = board.map((cell, i) => {
        cell.adjacents = tutorial.adjacents[i]
        return cell
      })
      store.dispatch('game/changeBoard', board)
      store.dispatch('game/changeSetting', { mode: 1 })
    }, timeouts.tutorial)
  }
}
